import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { SET_DATA } from 'store/actions';
import { useHistory } from 'react-router-dom';

// material-ui
import { Grid, MenuItem, Select, InputLabel, FormControl } from '@material-ui/core';

// project imports
import { gridSpacing } from 'store/constant';

import StockCard from 'views/dashboard/Default/StockCard';
import configData from 'config';

import axios from 'axios';
//==============================|| TYPOGRAPHY ||==============================//

const Strategies = () => {
    const [isLoading, setLoading] = useState(true);
    const [dataset, setDataset] = useState([{}]);
    const [subscriptions, setSubscriptions] = useState({});
    const [value, setValue] = useState("annualized_return");

    const account = useSelector((state) => state.account);
    let history = useHistory();

    const handleChange = (t) => {
        setValue(t.target.value);
    }

    const handleSubscriptions = (li) => {
        let subs = {};
        for (const x of li) {
            subs[x] = true;
        }
        setSubscriptions({...subscriptions, ...subs});
    }

    useEffect(() => {
        const url = configData.API_SERVER+"strategies?sort_by="+value;
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => setDataset(response.data))
          .then(() => setLoading(false))
    }, [value])

    useEffect(() => {
        const url = configData.API_SERVER+"strategies";
        axios.get(url, { headers: { Authorization: `${account.token}` } })
          .then((response) => setDataset(response.data))
          .then(() => setLoading(false))
    }, []);

    useEffect(() => {
        const url = configData.API_SERVER+"strategy_subscriptions";
        axios.get(url, { headers: { Authorization: `${account.token}` } })
            .then((response) => handleSubscriptions(response.data))
            // .then(() => setLoading(false))
    }, []);

    useEffect(() => {
        console.log(subscriptions);
    }, [subscriptions])

    return (
        <div>
            <Grid container spacing={gridSpacing}>
                <Grid item xs={12}>
                    <FormControl>
                        <InputLabel htmlFor="demo-simple-select">Sort</InputLabel>
                        <Select
                            labelId="demo-simple-select"
                            id="demo-simple-select"
                            value={value}
                            label="Sort"
                            onChange={handleChange}
                        >
                            <MenuItem value={"annualized_return"}>Annual Return</MenuItem>
                            <MenuItem value={"annualized_sharpe"}>Sharpe</MenuItem>
                            <MenuItem value={"mdd"}>Maximum Drawdown</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>

                {dataset.map(item => (
                    <Grid key={item["id"]+''} item xs={12} md={6} lg={4} xl={3}>
                        <div onClick={() => {!isLoading && history.push("/utils/strategies/"+item['id'])}}>
                            <StockCard isLoading={isLoading} subscribed={subscriptions[item["id"]] || false} data={item} />
                        </div>
                    </Grid>
                ))}
            </Grid>
        </div>
    );
};

export default Strategies;
